import React, {Fragment, useState} from 'react';
import MenuMobile from './MenuMobile';
import Carrito from './Carrito';

function NavBarMobile() {
    var [abrirMenu, setAbrirMenu]= useState(0)
    function disteClick(numero){
        if (abrirMenu === 1 || abrirMenu === 2){
            setAbrirMenu(abrirMenu = 0);
        } else{
            if (numero === 1){
                    setAbrirMenu(abrirMenu = 1);
                }
            if (numero === 2){
                setAbrirMenu(abrirMenu = 2);}
            }
    }
    return (
        <Fragment>
            <div className="nav">
                <i className="fas fa-store logo"></i>
                <form><input/></form>
                <div className="menu-cart">
                    <i className="fas fa-bars icon" onClick={() => disteClick(1)}></i>
                    <i className="fas fa-shopping-cart icon" onClick={() => disteClick(2)}></i>
                </div>
            </div>
            {abrirMenu === 1 ? (<MenuMobile/>) : (<div></div>)}
            {abrirMenu === 2 ? (<Carrito/>  ) : (<div></div>)}
        </Fragment>
    );
}

export default NavBarMobile;