import React,{Fragment} from 'react';

function ProductoMobile(){
    return(
        <Fragment>
            <div className="producto">
                <div className="foto"/>
                <div className="producto-info">
                    <span className="subtitulo">Nombre del Producto</span>
                    <span className="precio">$ 21,99</span>
                    <button className="boton">Agregar al carrito</button>
                </div>
            </div>
        </Fragment>    
    );
}
export default ProductoMobile;